.container {
    width: 768px;
    margin: 0 auto;
}

.profile {
    width: 250px;
    text-align: center;
    border-radius: 4px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 1px 3px 0px rgba(191, 205, 222, 0.75);
    padding-top: 30px;
}

.avatar {
    border-radius: 50%;
    box-shadow: inset 0px 1px 2px 0px rgba(0, 0, 0, 0.15);
    width: 99px;
    height: 99px;
}

.stats {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    border-style: solid;
    border-width: 1px;
    border-color: rgb(228, 233, 240);
    background-color: rgb(243, 246, 249);

}

.statsItem {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 14px;
    color: rgb(118, 134, 150);
    line-height: 2.667;
    text-align: center;
    width: 33.33333%;
}
  

  

  